@import url(https://fonts.googleapis.com/css?family=Roboto:100,100i,300,300i,400,400i,500,500i,700,700i,900,900i&display=swap);
body{
    color: #BBCDE5;
    background-color: #222222;
    padding: 0px;
    margin: 0px;
};

body::-webkit-scrollbar {
    width: 0em;
}
body::-webkit-scrollbar-track {
    -webkit-box-shadow: inset 0 0 0px red;
}
body::-webkit-scrollbar-thumb {
  background-color: red;
  outline: 1px solid red;
}
*{
    font-family: 'Roboto', sans-serif;
}

.nav__content {
    display: grid;
    grid-template-columns: 1fr 1.5fr;
    grid-row: 100vh;
    padding: 1em;
}



.grid__container{
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(260px, 1fr));
    overflow-y: auto;
    overflow-x: hidden;
    height: 94vh;
}

.grid__container > a:nth-child(n){
  text-align: center;
  text-decoration: none;
  -webkit-transition: 0.5s ease-in-out;
  transition: 0.5s ease-in-out;
  height: 298px;
}

.grid__container > a:nth-child(n){
  background-image: url(/portfolio/static/media/mbh.41967a37.PNG);
  background-size: cover;
}


.grid__container::-webkit-scrollbar {
  width: 0.4em;
}
.grid__container::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 6px transparent;
}
.grid__container::-webkit-scrollbar-thumb {
background-color: #BBCDE5;
}

.content__link{
  display: none;
}
.left__content{
    justify-content: left;	
    padding: 0 1em;
    height: auto;
}

h1{
    font-size: 3.12vw;
    margin-top: 0px;
}

p{
    font-size: 1.48vw;
}

li{
    list-style-type: disc;
    display: inline-block;
    font-size: 2.34vw;
    padding-right: 1em;
}

#root > div > div > div.left__content > section > ul > li:nth-child(n) > span{
    background-color: lime;
    height: 50px;
    width: 100%;
}
