.left__content{
    justify-content: left;	
    padding: 0 1em;
    height: auto;
}

h1{
    font-size: 3.12vw;
    margin-top: 0px;
}

p{
    font-size: 1.48vw;
}

li{
    list-style-type: disc;
    display: inline-block;
    font-size: 2.34vw;
    padding-right: 1em;
}

#root > div > div > div.left__content > section > ul > li:nth-child(n) > span{
    background-color: lime;
    height: 50px;
    width: 100%;
}